import React from "react";
import ReactDOM from "react-dom";
import Popup from "reactjs-popup";
import { nanoid } from "nanoid";

const popup = {
  borderWidth: 2,
  borderStyle: "solid",
  borderColor: "black",
  background: "lavender",
};
const popupBtns = {
  display: "flex",
  justifyContent: "center",
  flexDirection: "row",
};
const input = {
  marginTop: 80,
  display: "flex",
  justifyContent: "center",
  flexDirection: "column",
};
const inputField = {
  margin: 20,
  fontSize: 25,
  height: 40,
  width: 400,
  alignSelf: "center",
  paddingLeft: 10,
};
const inputForm = {
  fontSize: 20,
};
const inputBtn = {
  margin: 20,
  fontSize: 30,
  height: 60,
  width: 250,
};

const dataField = {
  margin: 20,
  fontSize: 25,
  height: 40,
  width: 300,
  alignSelf: "center",
  paddingLeft: 10,
  borderWidth: 2,
  borderStyle: "solid",
  borderColor: "black",
  background: "FloralWhite",
};

const display = {
  display: "flex",
  flexDirection: "column",
  alignItems: "center",
};

const deleteBtn = {
  margin: 20,
  fontSize: 30,
  height: 40,
  width: 60,
  borderWidth: 2,
  borderStyle: "solid",
  borderColor: "black",
  background: "LightCoral",
};

class App extends React.Component {
  constructor(props) {
    super(props);

    let personsList = [];
    if (localStorage.getItem("personsList")) {
      personsList = JSON.parse(localStorage.getItem("personsList"));
    }
    this.state = {
      personsList: personsList,
      firstName: "",
      lastName: "",
      address: "",
      phoneNumber: "",
      clicked: false,
    };

    this.addingNewEntry = this.addingNewEntry.bind(this);
    this.deleteAllData = this.deleteAllData.bind(this);
    this.sortDataByFirstName = this.sortDataByFirstName.bind(this);
    this.sortDataByLastName = this.sortDataByLastName.bind(this);
    this.sortDataByAddress = this.sortDataByAddress.bind(this);
    this.sortDataByPhoneNumber = this.sortDataByPhoneNumber.bind(this);
  }

  addingNewEntry = (event) => {
    const allPersons = [...this.state.personsList];
    this.setState(
      {
        personsList: [
          {
            firstName: this.state.firstName,
            lastName: this.state.lastName,
            address: this.state.address,
            phoneNumber: this.state.phoneNumber,
            id: nanoid(3),
          },
          ...allPersons,
        ],
        firstName: "",
        lastName: "",
        address: "",
        phoneNumber: "",
      },
      () => {
        localStorage.setItem(
          "personsList",
          JSON.stringify(this.state.personsList)
        );
      }
    );
    event.preventDefault();
  };

  deleteAllData() {
    this.setState(
      {
        personsList: [],
      },
      () => {
        localStorage.clear("personsList");
      }
    );
  }

  deleteDataRow(id) {
    const newPeoplesList = [...this.state.personsList].filter(
      (person) => person.id !== id
    );
    this.setState(
      {
        personsList: [...newPeoplesList],
      },
      () => {
        localStorage.setItem("personsList", JSON.stringify(newPeoplesList));
      }
    );
  }

  changeData(index, dataType, value) {
    const updateData = this.state.personsList.map((detail, i) => {
      if (i === index) {
        return { ...detail, [dataType]: value };
      }
      return detail;
    });

    this.setState(
      {
        personsList: updateData,
      },
      () => {
        localStorage.setItem(
          "personsList",
          JSON.stringify(this.state.personsList)
        );
      }
    );
  }

  sortDataByFirstName() {
    const myList = [].concat(this.state.personsList).sort((a, b) => {
      const nameA = a.firstName.toLowerCase();
      const nameB = b.firstName.toLowerCase();
      if (nameA < nameB) {
        return -1;
      }
      if (nameA > nameB) {
        return 1;
      }
      return 0;
    });
    this.setState({
      personsList: [...myList],
    });
  }

  sortDataByLastName() {
    const myList = [].concat(this.state.personsList).sort((a, b) => {
      const surnameA = a.lastName.toLowerCase();
      const surnameB = b.lastName.toLowerCase();
      if (surnameA < surnameB) {
        return -1;
      }
      if (surnameA > surnameB) {
        return 1;
      }
      return 0;
    });
    this.setState({
      personsList: [...myList],
    });
  }

  sortDataByAddress() {
    const myList = [].concat(this.state.personsList).sort((a, b) => {
      const addressA = a.address.toLowerCase();
      const addressB = b.address.toLowerCase();
      if (addressA < addressB) {
        return -1;
      }
      if (addressA > addressB) {
        return 1;
      }
      return 0;
    });
    this.setState({
      personsList: [...myList],
    });
  }

  sortDataByPhoneNumber() {
    const myList = [].concat(this.state.personsList).sort((a, b) => {
      const numberA = a.phoneNumber.toLowerCase();
      const numberB = b.phoneNumber.toLowerCase();
      if (numberA < numberB) {
        return -1;
      }
      if (numberA < numberB) {
        return 1;
      }
      return 0;
    });
    this.setState({
      personsList: [...myList],
    });
  }

  render() {
    return (
      <div style={display}>
        <div>
          <Popup trigger={<button style={inputBtn}>New Entry</button>} modal>
            {(close) => (
              <div style={popup}>
                <div>
                  <form style={input}>
                    <input
                      style={inputField}
                      type="text"
                      value={this.state.firstName}
                      onChange={(event) =>
                        this.setState({ firstName: event.target.value })
                      }
                      autoComplete="off"
                      placeholder="Enter First Name"
                    ></input>
                    <input
                      style={inputField}
                      type="text"
                      value={this.state.lastName}
                      onChange={(event) =>
                        this.setState({ lastName: event.target.value })
                      }
                      autoComplete="off"
                      placeholder="Enter Last Name"
                    ></input>
                    <input
                      style={inputField}
                      type="text"
                      value={this.state.address}
                      onChange={(event) =>
                        this.setState({ address: event.target.value })
                      }
                      autoComplete="off"
                      placeholder="Enter Address"
                    ></input>
                    <input
                      style={inputField}
                      type="text"
                      value={this.state.phoneNumber}
                      onChange={(event) =>
                        this.setState({ phoneNumber: event.target.value })
                      }
                      autoComplete="off"
                      placeholder="Enter Phone Number"
                    ></input>
                    <div style={popupBtns}>
                      <button style={inputBtn} onClick={this.addingNewEntry}>
                        Submit
                      </button>
                      <button
                        style={inputBtn}
                        onClick={() => {
                          close();
                        }}
                      >
                        Close Window
                      </button>
                    </div>
                  </form>
                </div>
              </div>
            )}
          </Popup>
          <button style={inputBtn} onClick={this.deleteAllData}>
            Clear All Data
          </button>
        </div>

        <div style={inputForm}>
          <form>
            <table>
              <thead>
                <tr>
                  <th>
                    <button
                      style={inputBtn}
                      type="button"
                      onClick={this.sortDataByFirstName}
                    >
                      First Name
                    </button>
                  </th>
                  <th>
                    <button
                      style={inputBtn}
                      type="button"
                      onClick={this.sortDataByLastName}
                    >
                      Second Name
                    </button>
                  </th>
                  <th>
                    <button
                      style={inputBtn}
                      type="button"
                      onClick={this.sortDataByAddress}
                    >
                      Address
                    </button>
                  </th>
                  <th>
                    <button
                      style={inputBtn}
                      type="button"
                      onClick={this.sortDataByPhoneNumber}
                    >
                      Phone Number
                    </button>
                  </th>
                </tr>
              </thead>
              {this.state.personsList.map((person, index) => {
                return (
                  <tbody>
                    <tr key={person.id}>
                      <td>
                        <input
                          style={dataField}
                          onChange={(e) =>
                            this.changeData(index, "firstName", e.target.value)
                          }
                          type="text"
                          placeholder="no data"
                          value={this.state.personsList[index].firstName}
                        />
                      </td>
                      <td>
                        <input
                          style={dataField}
                          onChange={(e) =>
                            this.changeData(index, "lastName", e.target.value)
                          }
                          type="text"
                          placeholder="no data"
                          value={this.state.personsList[index].lastName}
                        />
                      </td>
                      <td>
                        <input
                          style={dataField}
                          onChange={(e) =>
                            this.changeData(index, "address", e.target.value)
                          }
                          type="text"
                          placeholder="no data"
                          value={this.state.personsList[index].address}
                        />
                      </td>
                      <td>
                        <input
                          style={dataField}
                          onChange={(e) =>
                            this.changeData(
                              index,
                              "phoneNumber",
                              e.target.value
                            )
                          }
                          type="text"
                          placeholder="no data"
                          value={this.state.personsList[index].phoneNumber}
                        />
                      </td>
                      <td>
                        <button
                          style={deleteBtn}
                          onClick={() => {
                            this.deleteDataRow(person.id);
                          }}
                        >
                          x
                        </button>
                      </td>
                    </tr>
                  </tbody>
                );
              })}
            </table>
          </form>
        </div>
      </div>
    );
  }
}

ReactDOM.render(<App />, document.getElementById("root"));
